import GridContainer from "./../../helpers/GridContainer";
import ChristValueIcon from "./../../assets/christ-icon.svg";
import BadenWurttemberIcon from "./../../assets/baden-wurttemberg-icon.svg";
import ProgressIcon from "./../../assets/progress-icon.svg";
import DialogIcon from "./../../assets/dialog-icon.svg";
import JusticeIcon from "./../../assets/justice-icon.svg";
import TreeIcon from "./../../assets/tree-icon.svg";

const values = [
  {
    icon: TreeIcon,
    title: "Wurzeln und Prgung",
    content:
      "Geprgt haben mich mein modernes und weltoffenes Elternhaus, sowie bodenstndige Groeltern der Nachkriegsgeneration mit Wurzeln in Handwerk und Landwirtschaft.",
  },
  {
    icon: ChristValueIcon,
    title: "Glaube und Orientierung",
    content:
      "Meine Familie und die kirchliche Jugendarbeit haben es mir ermglicht, meinen Weg zum christlichen Glauben und zur Kirche zu finden. Auch hier gilt fr mich: Nicht nur drber reden  christliche Werte im Alltag leben!",
  },
  {
    icon: BadenWurttemberIcon,
    title: "Heimat und Freiheit",
    content:
      "Mir liegt unsere Zukunft und die der folgenden Generationen am Herzen. Ich brenne fr unser Land, fr unsere Demokratie und fr unsere Freiheit.",
  },
  {
    icon: ProgressIcon,
    title: "Leistung und Flei",
    content:
      "Wohlstand und Sicherheit gibt es aber nicht zum Nulltarif. Unsere Gesellschaft lebt vom Leistungswillen, von Flei und der Arbeit derjenigen, die hier leben.",
  },
  {
    icon: DialogIcon,
    title: "Gemeinsamer Dialog",
    content:
      "Der intensive Austausch mit Ihnen ist mir wichtig. Ich mchte Ihnen zuhren und mit Ihnen auf Augenhhe kommunizieren. Dialog und das argumentative Ringen um die besten Lsungen sind der Schlssel zum gemeinsamen Erfolg.",
  },
  {
    icon: JusticeIcon,
    title: "Konstruktiv und Ehrlich",
    content:
      "Spaltung und radikaler Populismus ist das Gegenmodel zu der Politik, fr die ich mich stark machen werde!",
  },
];

function OneValue({ icon, title, content }) {
  return (
    <div className="one-value">
      <img src={icon} alt={icon} className="icon" />
      <span className="title">{title}</span>
      <span className="content">{content}</span>
    </div>
  );
}

function MyValues() {
  return (
    <GridContainer className="my-values with-header blue-background" id="values">
      <h1 className="my-values__header">
        <span>Meine Werte</span>
      </h1>
      <div className="my-values__content">
        <div className="my-values__content__description">
          Warum bewerbe ich mich fr die Nominierung als Landtagskandidat? Mir
          liegt unsere Zukunft und die der folgenden Generationen am Herzen.{" "}
          <strong>
            Ich brenne fr unser Land, fr unsere Demokratie und fr unsere
            Freiheit.{" "}
          </strong>
          Wohlstand und Sicherheit gibt es aber nicht zum Nulltarif. Unsere
          Gesellschaft lebt vom{" "}
          <strong>Leistungswillen, von Flei und der Arbeit</strong>
          derjenigen, die hier leben. Lassen Sie uns deshalb gemeinsam mit Mut,
          Zuversicht und Erfahrung die enormen Herausforderungen anpacken.
          <strong>Baden-Wrttemberg kann mehr.</strong> Ich werde dazu
          beitragen, dass unser Land wieder an der Spitze steht!
        </div>
        <div className="my-values__content__all-values">
          {values.map((value) => {
            return <OneValue {...value} />;
          })}
        </div>
      </div>
    </GridContainer>
  );
}

export default MyValues;
